@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100..900&display=swap');


* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-style: normal;
}

body {
  &::-webkit-scrollbar {
    display: none;
  }
}

.App {
  width: 100vw;
  height: 100vh;
  position: absolute;
  overflow: hidden;

  #viewer {
    width: 100vw;
    position: absolute;
    top: 0vh;
    transition: 1s;
  }
}


#intro {
  position: relative;
  width: 100vw;
  height: 100vh;
  background-color: black;
  z-index: 2;

  &>* {
    user-select: none;
  }

  h1 {
    font-size: 6vw;
    color: white;
    font-weight: 700;
    position: absolute;
    text-align: center;
    left: 0;
    right: 0;
    top: 17.5vh;
  }

  h2 {
    font-size: 1.25vw;
    color: #C2C2C2;
    font-weight: 400;
    text-align: center;
    position: absolute;
    left: 0;
    right: 0;
    top: 35vh;
  }

  h3 {
    font-size: 1.25vw;
    color: #2a7be6;
    font-weight: 700;
    text-align: center;
    position: absolute;
    left: 0;
    right: 0;
    top: 17.5vh;
  }
}

.elementContainer {
  position: absolute;
  bottom: 0;
  width: 200%;
  height: 50%;


  .elementBar {
    width: 200%;
    height: 50%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    div {
      width: 20vw;
      height: 11vw;
      background-color: rgba(#2e7df9, .15);
      border-radius: 1.5vw;
      background-size: 10%;
    }
  }

  // 왼쪽으로 이동하는 애니메이션
  .left {
    animation: scrollLeft 60s linear infinite; // 왼쪽으로 이동
  }

  // 오른쪽으로 이동하는 애니메이션
  .right {
    animation: scrollRight 60s linear infinite; // 오른쪽으로 이동
  }
}

@keyframes scrollLeft {
  0% {
    transform: translateX(0); // 처음 위치
  }

  100% {
    transform: translateX(-67%); // 왼쪽 끝으로 이동
  }
}

@keyframes scrollRight {
  0% {
    transform: translateX(-67%); // 처음 위치
  }

  100% {
    transform: translateX(0%); // 오른쪽 끝으로 이동
  }
}

#main {
  width: 100vw;
  height: 100vh;
  position: relative;


  #currentPostBox {
    width: 70%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 0 .5vw 0 rgba(black, .2);
    overflow: scroll;

    &::-webkit-scrollbar {
      display: none;
    }

    #currentPost {
      width: 80%;
      height: 80%;

      #postFooter {
        height: 15vh;
      }
    }
  }
}

#header {
  width: 100vw;
  position: relative;
  top: -6vh;
  height: 15vh;
  border-bottom: .1vw solid rgba(black, .1);
  transition: .5s;
  padding-left: 10vw;
  padding-right: 10vw;
  background-color: white;
  z-index: 1;

  &.active {
    top: 0vh;
  }

  .top {
    display: flex;
    height: 40%;
    justify-content: space-between;
    align-items: center;

    .timeline {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 12.3vw;
      height: 100%;
      font-weight: 700;
      font-size: .7vw;
      color: #2a7be6;

      span {
        margin-top: .1vw;
        font-size: 1.2vw;
      }
    }

    .btnBox {
      width: 10vw;
      height: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;

      .btn {
        height: 60%;
        width: 5vw;
        height: 2vw;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: .5vw;
        cursor: pointer;
        user-select: none;
        color: rgba(black, .8);

        &:nth-child(2) {
          margin-top: .15vw;
          font-variation-settings:
            'FILL'1
        }

        span {
          margin-top: .1vw;
          font-size: 1vw;
        }

        p {
          margin-left: .15vw;
          font-size: .7vw;
        }

        &:hover {
          background-color: rgba(#2e7df9, .2);
          color: #2e7df9;
          transition: .1s;
          font-variation-settings:
            'FILL'1
        }
      }
    }
  }

  .bottom {
    display: flex;
    height: 60%;
    position: relative;
    align-items: center;

    img {
      position: absolute;
      left: 0;
      width: 8vw;
    }

    .categoryBox {
      position: absolute;
      width: 9.5vw;
      height: 100%;
      right: 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
      user-select: none;

      .category {
        cursor: pointer;
        display: flex;
        width: 4vw;
        height: 4vw;
        font-weight: 500;
        color: rgba(black, .6);
        border-radius: 25%;

        flex-direction: column;
        justify-content: center;
        align-items: center;

        span {
          font-size: 1.75vw;
          font-variation-settings:
            'FILL'1
        }

        p {
          font-size: .6vw;
        }

        &:hover {
          background-color: rgba(black, .05);

        }

        &.active {
          color: #2a7be6;
          font-weight: 700;
        }
      }
    }
  }
}

#list {
  position: relative;
  top: -6vh;
  left: 0;
  right: 0;
  margin: auto;
  width: 80%;
  height: 100%;

  #nav {
    width: 9%;
    height: 7%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: absolute;
    right: 0;

    >*{
      width: 2vw;
      height: 2vw;
      font-size: 1vw;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 20%;
      user-select: none;
    }

      .material-symbols-outlined {
        background-color: white;
        border: .1vw solid rgba(black, .2);
        color: black;
        cursor: pointer;

        &.locked{
          color: rgba(black, .4);
          cursor: auto;

          &:hover{
            background-color: white;
          }
        }
        &:hover{
          background-color: rgba(black, .08);
        }
      }
      p{
        background-color: #2a7be6;
        color: white;
      }
    }
    .postBox {
      width: 100%;
      top: 7%;
      position: absolute;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(3, 1fr);
      gap: 2vw; /* 요소 간 간격 조절 */
    }
    
    .post {
      width: 100%; 
      height: 24.5vh; 
      display: flex;
      align-items: center;
      justify-content: center;
      box-sizing: border-box;
      padding: 10px;
      border-radius: .5vw;
      box-shadow: 0 0 .1vw 0 rgba(black, .4);
      transition: .1s;
      position: relative;
      top: 0;
      cursor: pointer;

      &:hover{
        position: relative;
        top: -.2vh;
      }

      .ref{
        position: absolute;
        left: 1.5vw;
        top: 1vw;
        font-weight: 700;
      }

      .date{
        position: absolute;
        left: 5.25vw;
        top: 1vw;
        color: rgba(black,.6);
      }
      
      .tagBox{
        position: absolute;
        left: 1.5vw;
        top: 2.75vw;
        width: 90%; 
        height: 12.5%;
        display: flex;
        align-items: center;
        gap: .5vw;
        
        .tag{
          background-color: rgba(#2a7be6, $alpha: .1);
          padding: .15vw;
          padding-right: .35vw;
          padding-left: .35vw;
          border-radius: .25vw;
        }
      }
      .title{
        position: absolute;
        left: 1.5vw;
        font-weight: 500;
        font-size: 1vw;
      }

      .subTitle{
        position: absolute;
        left: 1.5vw;
        font-weight: 400;
        font-size: .8vw;
        top: 7vw;
        width: 87%;

        p{
          color: #2a7be6;
          font-weight: 600;
        }
      }
    }
    
    
}